.offer{
    min-height: 1356px;
    border-bottom: 1px solid #D9D9DE;
    background: #efe3e3;

    border-bottom: #C4C4C4;

    &__title{
        padding: 76px 0 40px;
    }

    &__wrapper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 32px;
    }
    

    &__link{
        display: block;
        width: 256px;
        height: 256px;
        border-radius: 6px;
        background-image: url(../images/offer/one.jpg);
        position: relative;

        &-title{
            font-family: 'Roboto', sans-serif;
            font-weight: 500;
            font-size: 18px;
            line-height: 130%;
            max-width: 218px;

            position: absolute;
            top: 10%;
            left: 10%;
            

            
        }

        &-subtitle{
            font-size: 16px;
            font-family: 'Roboto', sans-serif;
            font-weight: normal;
            position: absolute;
            left: 10%;
            bottom: 10%;
            
        }
        &:nth-child(2){
            background-image: url(../images/offer/two.jpg);
        }

        &:nth-child(3){
            grid-area: 1/3/3/5;
            width: 544px;
            height: 544px;
            background-image: url(../images/offer/3.jpg);
        }

        &:nth-child(3) .offer__link-title{
            width: 407px;
        }
        
        &:nth-child(4){
            background-image: url(../images/offer/four.jpg);
        }
        &:nth-child(5){
            background-image: url(../images/offer/five.jpg);
        }

        &:nth-child(6){
            background-image: url(../images/offer/six.jpg);
        }

        &:nth-child(7){
            background-image: url(../images/offer/seven.jpg);
        }

        &:nth-child(8){
            background-image: url(../images/offer/eight.jpg);
        }

        &:nth-child(9){
            background-image: url(../images/offer/nine.jpg);
        }

        &:nth-child(10){
            background-image: url(../images/offer/ten.jpg);
        }

        &:nth-child(11){
            background-image: url(../images/offer/eleven.jpg);
        }

        &:nth-child(12){
            background-image: url(../images/offer/twelve.jpg);
        }

        &:nth-child(13){
            background-image: url(../images/offer/thirteen.jpg);
            font-size: 24px;
            //text-align: center;
            vertical-align: middle;
        }
    }
}

